.idea-tile {
    margin: 10px;
    width: 150px;
    height: 150px;
    background-color: orange;
    position: relative;
    display: inline-block;
    vertical-align: top;
}

.idea-tile:hover .delete-button {
    display: block;
}

.idea-tile input {
    width: 80%;
    background-color: transparent;
    border: 0px solid;
}

.idea-tile .tile-create-date {
    font-size: 10px;
    font-weight: bold;
}

.idea-tile .tile-id {
    font-size: 10px;
    font-weight: bold;
}

.delete-button {
    position: absolute;
    top: 0;
    right: 0;
    background-color: red;
    display: none;
    cursor: pointer;
}
html, body {
  margin: 0;
  padding: 0;
}

button {
  margin: 0;
  padding: 0;
  border: 0;
  background: none;
  font-size: 100%;
  vertical-align: baseline;
  font-family: inherit;
  font-weight: inherit;
  color: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 1.4em;
  min-width: 230px;
  max-width: 550px;
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
}
